"use client";

import { Fragment, useState, useEffect } from "react";
import { Dialog, Transition } from "@headlessui/react";
import { useSession } from "next-auth/react";
import { usePathname, useRouter } from "next/navigation";
import { XMarkIcon } from "@heroicons/react/24/outline";
import Header from "@/components/dashboard/Header";

// Tipos para os componentes
type LoaderSize = "sm" | "md" | "lg";

type LoaderProps = {
  size?: LoaderSize;
  className?: string;
};

type NavigationProps = {
  pathname: string;
  variant?: "desktop" | "mobile";
};

// Componente de carregamento simples
const Loader = ({ size = "md", className = "" }: LoaderProps) => {
  const sizeClasses: Record<LoaderSize, string> = {
    sm: "h-4 w-4",
    md: "h-8 w-8",
    lg: "h-12 w-12",
  };
  
  return (
    <div className={`animate-spin rounded-full border-t-2 border-b-2 border-indigo-500 ${sizeClasses[size]} ${className}`}></div>
  );
};

// Componente de navegação simples
const Navigation = ({ pathname, variant = "desktop" }: NavigationProps) => {
  const isActive = (path: string) => pathname.startsWith(path);
  
  return (
    <nav className="flex-1">
      <ul className="space-y-1">
        <li>
          <a
            href="/dashboard"
            className={`flex items-center px-2 py-2 text-sm rounded-md ${
              isActive('/dashboard') && !isActive('/dashboard/') 
                ? 'bg-indigo-50 text-indigo-600 dark-cyber:bg-dark-cyber-muted/20 dark-cyber:text-dark-cyber-accent' 
                : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900 dark-cyber:text-dark-cyber-text dark-cyber:hover:bg-dark-cyber-muted/10'
            }`}
          >
            Dashboard
          </a>
        </li>
        <li>
          <a
            href="/dashboard/credentials"
            className={`flex items-center px-2 py-2 text-sm rounded-md ${
              isActive('/dashboard/credentials') 
                ? 'bg-indigo-50 text-indigo-600 dark-cyber:bg-dark-cyber-muted/20 dark-cyber:text-dark-cyber-accent' 
                : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900 dark-cyber:text-dark-cyber-text dark-cyber:hover:bg-dark-cyber-muted/10'
            }`}
          >
            Credenciais
          </a>
        </li>
        <li>
          <a
            href="/dashboard/iam"
            className={`flex items-center px-2 py-2 text-sm rounded-md ${
              isActive('/dashboard/iam') 
                ? 'bg-indigo-50 text-indigo-600 dark-cyber:bg-dark-cyber-muted/20 dark-cyber:text-dark-cyber-accent' 
                : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900 dark-cyber:text-dark-cyber-text dark-cyber:hover:bg-dark-cyber-muted/10'
            }`}
          >
            IAM
          </a>
        </li>
      </ul>
    </nav>
  );
};

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const { data: session, status } = useSession();
  const router = useRouter();
  const pathname = usePathname();
  const [isDarkCyber, setIsDarkCyber] = useState(false);
  
  // Verificar o status do tema dark-cyber
  useEffect(() => {
    const checkTheme = () => {
      if (typeof window !== 'undefined') {
        setIsDarkCyber(document.body.classList.contains('dark-cyber'));
      }
    };
    
    checkTheme();
    
    // Observar mudanças no tema
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.attributeName === 'class') {
          checkTheme();
        }
      });
    });
    
    if (typeof window !== 'undefined') {
      observer.observe(document.body, { attributes: true });
    }
    
    return () => observer.disconnect();
  }, []);

  // Redirecionar se não autenticado
  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/login");
    }
  }, [status, router]);

  if (status === "loading") {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Loader size="lg" className="text-indigo-600 dark-cyber:text-dark-cyber-primary" />
      </div>
    );
  }

  return (
    <>
      <div className={isDarkCyber ? "dark-cyber" : ""}>
        <Transition.Root show={sidebarOpen} as={Fragment}>
          <Dialog
            as="div"
            className="relative z-50 lg:hidden"
            onClose={setSidebarOpen}
          >
            <Transition.Child
              as={Fragment}
              enter="transition-opacity ease-linear duration-300"
              enterFrom="opacity-0"
              enterTo="opacity-100"
              leave="transition-opacity ease-linear duration-300"
              leaveFrom="opacity-100"
              leaveTo="opacity-0"
            >
              <div className="fixed inset-0 bg-gray-900/80 dark-cyber:bg-dark-cyber-bg/80" />
            </Transition.Child>

            <div className="fixed inset-0 flex">
              <Transition.Child
                as={Fragment}
                enter="transition ease-in-out duration-300 transform"
                enterFrom="-translate-x-full"
                enterTo="translate-x-0"
                leave="transition ease-in-out duration-300 transform"
                leaveFrom="translate-x-0"
                leaveTo="-translate-x-full"
              >
                <Dialog.Panel className="relative mr-16 flex w-full max-w-xs flex-1">
                  <Transition.Child
                    as={Fragment}
                    enter="ease-in-out duration-300"
                    enterFrom="opacity-0"
                    enterTo="opacity-100"
                    leave="ease-in-out duration-300"
                    leaveFrom="opacity-100"
                    leaveTo="opacity-0"
                  >
                    <div className="absolute left-full top-0 flex w-16 justify-center pt-5">
                      <button
                        type="button"
                        className="-m-2.5 p-2.5"
                        onClick={() => setSidebarOpen(false)}
                      >
                        <span className="sr-only">Fechar sidebar</span>
                        <XMarkIcon
                          className="h-6 w-6 text-white"
                          aria-hidden="true"
                        />
                      </button>
                    </div>
                  </Transition.Child>
                  
                  {/* Sidebar mobile */}
                  <div className="flex grow flex-col gap-y-5 overflow-y-auto bg-white dark-cyber:bg-dark-cyber-card px-6 pb-4">
                    <div className="flex h-16 shrink-0 items-center">
                      <h1 className="text-indigo-600 dark-cyber:text-dark-cyber-primary font-bold text-xl">AWS Monitor</h1>
                    </div>
                    <Navigation pathname={pathname} variant="mobile" />
                  </div>
                </Dialog.Panel>
              </Transition.Child>
            </div>
          </Dialog>
        </Transition.Root>

        {/* Sidebar desktop */}
        <div className="hidden lg:fixed lg:inset-y-0 lg:z-50 lg:flex lg:w-72 lg:flex-col">
          <div className="flex grow flex-col gap-y-5 overflow-y-auto border-r border-gray-200 dark-cyber:border-dark-cyber-border bg-white dark-cyber:bg-dark-cyber-card px-6 pb-4">
            <div className="flex h-16 shrink-0 items-center">
              <h1 className="text-indigo-600 dark-cyber:text-dark-cyber-primary font-bold text-xl">AWS Monitor</h1>
            </div>
            <Navigation pathname={pathname} variant="desktop" />
          </div>
        </div>

        {/* Header e conteúdo principal */}
        <div className="lg:pl-72">
          <Header
            sidebarOpen={sidebarOpen}
            setSidebarOpen={setSidebarOpen}
            user={session?.user}
          />

          <main className="py-10">
            <div className="px-4 sm:px-6 lg:px-8">{children}</div>
          </main>
        </div>
      </div>
    </>
  );
} 