import { Fragment } from "react";
import { Menu, Transition } from "@headlessui/react";
import { signOut } from "next-auth/react";

// Ícones
import {
  Bars3Icon,
  UserCircleIcon,
  MoonIcon,
  SunIcon,
} from "@heroicons/react/24/outline";

function classNames(...classes: string[]) {
  return classes.filter(Boolean).join(" ");
}

export default function Header({
  sidebarOpen,
  setSidebarOpen,
  user,
}: {
  sidebarOpen: boolean;
  setSidebarOpen: (open: boolean) => void;
  user: any;
}) {
  const toggleDarkCyberMode = () => {
    if (typeof window !== 'undefined' && (window as any).toggleTheme) {
      (window as any).toggleTheme();
    }
  };

  const isDarkCyberMode = typeof window !== 'undefined' ? (window as any).darkCyberMode : false;

  return (
    <div className="sticky top-0 z-40 flex h-16 shrink-0 items-center gap-x-4 border-b border-gray-200 bg-white px-4 shadow-sm sm:gap-x-6 sm:px-6 lg:px-8">
      <button
        type="button"
        className="-m-2.5 p-2.5 text-gray-700 lg:hidden"
        onClick={() => setSidebarOpen(true)}
      >
        <span className="sr-only">Abrir sidebar</span>
        <Bars3Icon className="h-6 w-6" aria-hidden="true" />
      </button>

      {/* Separador */}
      <div className="h-6 w-px bg-gray-200 lg:hidden" aria-hidden="true" />

      <div className="flex flex-1 gap-x-4 self-stretch lg:gap-x-6 items-center justify-end">
        {/* Botão de tema */}
        <button
          type="button"
          onClick={toggleDarkCyberMode}
          className="flex items-center gap-x-1 text-sm font-medium text-gray-700 hover:text-indigo-600 p-2 rounded-md hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-indigo-500 dark-cyber:hover:bg-dark-cyber-muted"
          title={isDarkCyberMode ? "Mudar para tema claro" : "Mudar para tema cyber"}
        >
          {isDarkCyberMode ? (
            <SunIcon className="h-5 w-5 text-yellow-500" aria-hidden="true" />
          ) : (
            <MoonIcon className="h-5 w-5 text-indigo-600" aria-hidden="true" />
          )}
          <span className="hidden md:block">
            {isDarkCyberMode ? "Tema Light" : "Tema Cyber"}
          </span>
        </button>

        {/* Perfil do usuário */}
        <div className="flex items-center gap-x-4 lg:gap-x-6">
          <Menu as="div" className="relative">
            <Menu.Button className="-m-1.5 flex items-center p-1.5">
              <span className="sr-only">Abrir menu do usuário</span>
              <UserCircleIcon
                className="h-8 w-8 text-gray-300"
                aria-hidden="true"
              />
              <span className="hidden lg:flex lg:items-center">
                <span
                  className="ml-4 text-sm font-medium text-gray-700 group-hover:text-gray-900"
                  aria-hidden="true"
                >
                  {user?.name || "Usuário"}
                </span>
              </span>
            </Menu.Button>
            <Transition
              as={Fragment}
              enter="transition ease-out duration-100"
              enterFrom="transform opacity-0 scale-95"
              enterTo="transform opacity-100 scale-100"
              leave="transition ease-in duration-75"
              leaveFrom="transform opacity-100 scale-100"
              leaveTo="transform opacity-0 scale-95"
            >
              <Menu.Items className="absolute right-0 z-10 mt-2.5 w-32 origin-top-right rounded-md bg-white py-2 shadow-lg ring-1 ring-gray-900/5 focus:outline-none">
                <Menu.Item>
                  {({ active }) => (
                    <button
                      className={classNames(
                        active ? "bg-gray-50" : "",
                        "block px-3 py-1 text-sm leading-6 text-gray-900 w-full text-left"
                      )}
                      onClick={() => signOut()}
                    >
                      Sair
                    </button>
                  )}
                </Menu.Item>
              </Menu.Items>
            </Transition>
          </Menu>
        </div>
      </div>
    </div>
  );
} 